<!-- components/ChatList.vue -->
<template>
  <div class="w-full h-full overflow-y-scroll no-scrollbar">
    <div class="px-5 py-3 border-b border-lighter flex">
      <h1 class="text-xl font-bold flex-1">Chats</h1>
      <i class="fas fa-cog text-xl text-blue cursor-pointer"></i>
      <i @click="$emit('new-message')" class="fas fa-plus-circle ml-3 text-xl text-blue cursor-pointer"></i>
    </div>

    <Loader :loading="loading" />

    <div v-if="!loading">
      <div v-if="chats.length === 0" class="flex flex-col items-center justify-center w-full pt-10">
        <div class="w-1/2 flex flex-col items-center justify-center">
          <p class="font-bold text-lg">No chats yet</p>
          <p class="text-sm text-dark">Wait until someone starts a chat here.</p>
        </div>
      </div>

      <div v-for="chat in chats" :key="chat.id">
        <div
            class="cursor-pointer"
            :class="chat.id === activeChatId ? 'border-r-2 border-blue' : ''"
            @click="$emit('select-chat', chat)"
        >
          <div class="w-full p-2 pt-1 pb-1 md:p-4 md:pt-2 md:pb-2 border-b hover:bg-lightest flex">
            <div class="flex-none mr-2 md:mr-4 pt-1">
              <a :href="`#/${chat.other_user_id}`">
                <img
                    :src="getUser(chat.other_user_id).avatar || 'default_profile.png'"
                    class="w-12 h-12 rounded-full"
                />
              </a>
            </div>
            <div class="w-full truncate">
              <div class="flex items-center w-full">
                <p class="font-semibold">
                  {{ getUser(chat.other_user_id).username || 'Anonymous' }}
                </p>
                <p class="hidden md:block text-sm text-dark ml-2 truncate">
                  @{{ chat.other_user_id }}
                </p>
                <p class="text-sm text-dark ml-auto">
                  {{ $filters.timeago(chat.lastModified || chat.updated_at) }}
                </p>
              </div>
              <p class="pb-2 truncate" v-linkify>
                {{ chat.last_message || '' }}
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ChatList",
  props: {
    chats: [],
    loading: false,
    activeChatId: null,
    getUser: undefined,
  },
};
</script>
